		//加载更多时执行的函数
		loadMoreFunc() {
			//如果page > 2 代表加载了全部数据
			if (page > 1) {
				this.isEnd = true;
				this.loadingType = 2;
				return;
			}
			//展示loading
			this.loadingType = 1;
			//追加数据(延迟1秒 模拟网络请求)
			var that = this;
			setTimeout(function() {
				that.loading = false;
				var newData = that.getArrRandomly(that.demoDate);
				that.demoDate = that.demoDate.concat(newData);
				//累加页码
				page++;
				that.loadingType = 0;
			}, 1000);
		},
		getArrRandomly(arr) {
			var len = arr.length;
			for (var i = 0; i < len; i++) {
				var randomIndex = Math.floor(Math.random() * (len - i));
				var itemAtIndex = arr[randomIndex];
				arr[randomIndex] = arr[i];
				arr[i] = itemAtIndex;
			}
			return arr;
		},  
		change:function(e)
		{
			this.checkData.forEach(e =>{
				e.checkitem = false;
			})
			var a =e.currentTarget.dataset.index;
			this.checkData[a].checkitem = true;
		}
